<template>
  <div class="setting">
    <el-form
      :inline="true"
      :model="setting"
      @submit.native.prevent="onSubmit"
      class="form-search"
      label-width="200px"
      v-loading="loading"
    >
      <div class="searchs">
        <el-row :gutter="24">
          <el-col v-for="(v, k) in white_list" :key="k">
            <el-form-item label="白名单手机号">
              <el-input v-model="white_list[k]" size="small" placeholder="请输入手机号"> </el-input>
            </el-form-item>
            <el-form-item class="delete">
              <el-button @click.prevent="removeConfig(k)" v-show="white_list.length > 1">删除</el-button>
              <el-button @click="addConfig" v-show="white_list.length - 1 === k">添加</el-button>
            </el-form-item>
          </el-col>
          <el-col>
            <el-form-item label="安全码">
              <el-input v-model="security_code" size="small" placeholder="请输入安全码"> </el-input>
            </el-form-item>
          </el-col>

          <el-col>
            <el-form-item>
              <el-button type="primary" size="small" native-type="submit">保存</el-button>
            </el-form-item>
          </el-col>
        </el-row>
      </div>
    </el-form>
  </div>
</template>
<script>
import { Message } from 'element-ui'
import Fetch from '../../utils/fetch'
import Fetch2 from '../../utils/fetch2'
import Fetch3 from '../../utils/fetch3'

export default {
  data() {
    return {
      loading: false,
      security_code: '',
      white_list: [],
      setting: {},
    }
  },
  mounted() {
    this.loading = true
    Fetch2('/sys/config/infowhitelistmanage', {})
      .then(r => {
        this.setting = r.data
        this.security_code = r.data.security_code
        this.loading = false
        this.white_list = r.data.white_list
      })
      .catch(() => (this.loading = false))
  },
  methods: {
    addConfig() {
      if (this.white_list.length >= 10) {
        return
      }
      this.white_list.push('')
    },

    removeConfig(index) {
      this.white_list.splice(index, 1)
    },
    onSubmit() {
      this.loading = true
      var tel_arr = []
      this.white_list.forEach(k => {
        tel_arr.push(Number(k))
      })
      Fetch3('/sys/config/infowhitelistmanage', {
        type: 'white_list_manage',
        security_code: this.security_code,
        white_list: JSON.stringify(tel_arr),
      })
        .then(() => {
          Message.success('更新成功')
          this.loading = false
        })
        .catch(() => {
          Message.error('更新失败')
          this.loading = false
        })
    },
  },
}
</script>
<style lang="less" scoped>
.setting {
  > div {
    padding: 14px 14px 14px 15px;
  }

  .searchs {
    background-color: #f2f2f2;
    border-left: 5px solid #1aa094;
    color: #606266;
    font-size: 16px;
  }

  /deep/ .el-input__inner {
    width: 300px;
  }

  /deep/ .el-textarea {
    width: 300px;
  }
}
</style>
